import React from "react";
import Nav from "../../components/Navbar/Nav";
import { useState, useEffect } from "react";
import { useCart } from "../../Context/cart";
import { useAuth } from "../../Context/auth";
import { useNavigate } from "react-router-dom";
import { toast } from "react-hot-toast";
import { FaRupeeSign } from "react-icons/fa";
import DropIn from "braintree-web-drop-in-react";

import { CgArrowLongRight } from "react-icons/cg";
import { NavLink } from "react-router-dom";
import emptycart from "../../Images/cartpage/emptycartbanner.jpg";
import axios from "axios";

const Cart = () => {
  const [cart, setCart] = useCart();
  const [auth] = useAuth();
  const [instance, SetInstance] = useState("");
  const [Loading, SetLoading] = useState(false);
  const [clientToken, SetClientToken] = useState("");
  const Navigate = useNavigate();

  //total price
  const totalPrice = () => {
    try {
      let total = 0;
      cart?.map((item) => {
        return (total = total + item.price);
      });
      return total.toLocaleString("rupee", {
        style: "currency",
        currency: "INR",
      });
    } catch (error) {
      console.log(error);
    }
  };
  const removeCartItem = (pid) => {
    try {
      // const yesRemove = window.prompt(
      //   "Are you sure to remove this product from the cart? if yes Type 'REMOVE' "
      // );
      // if (yesRemove === "REMOVE") {
      let myCart = [...cart];
      let index = myCart.findIndex((item) => item._id === pid);
      myCart.splice(index, 1);
      toast.success(`item removed successfully`);
      setCart(myCart);
      localStorage.setItem("cart", JSON.stringify(myCart));
      // }
      // return;
    } catch (error) {
      console.log(error);
    }
  };
  const getToken = async () => {
    try {
      const { data } = await axios.get("/api/admin/product/braintree/token");
      if (data) {
        SetClientToken(data?.clientToken);
        // console.log(data);
      } else {
        console.log("cannot get token");
      }
    } catch (error) {
      console.log(error);
      toast.error("something went wrong");
    }
  };
  useEffect(() => {
    getToken();
  }, [auth?.token]);

  const HandlePayment = async () => {
    try {
      SetLoading(true);
      const nonce = await instance.requestPaymentMethod();
      const { data } = await axios.post(
        "/api/admin/product/braintree/payment",
        { nonce, cart }
      );
      console.log(data);
      SetLoading(false);
      localStorage.removeItem("cart");
      setCart([]);
      Navigate("/profile/user/orders");
      toast.success("payment complete successfully");
    } catch (error) {
      console.log(error.message);
      console.log(error);
      toast.error("something went wrong");
      SetLoading(false);
    }
  };

  return (
    <div>
      <Nav />
      <div className="container">
        <div className="row">
          <div className="col-md-12">
            <h1 className=" bg-light py-1 mb-1 text-uppercase">
              {`Hello ${auth?.token && auth?.user?.userName}!`}
            </h1>
            <h4 className="mb-3">
              {cart?.length ? (
                `you Have ${cart.length} items in your cart ${
                  auth?.token ? "" : "Please login to checkOut !"
                }`
              ) : (
                <span className="bg-warning rounded-3 p-1 px-3 pe-5 fw-bold ">
                  OH! your cart is empty..{" "}
                </span>
              )}
            </h4>
          </div>
        </div>
        <div className="row ">
          <div className="col-md-8 ">
            <div className="row">
              <div>
                {cart?.length ? (
                  <>
                    <NavLink to="/allproducts">
                      <button className="button-34 h2 text-uppercase mt-3">
                        Add More <CgArrowLongRight />
                      </button>
                    </NavLink>
                  </>
                ) : (
                  <>
                    <div className=" d-flex flex-column gap-2">
                      <div>
                        <img
                          src={emptycart}
                          style={{ opacity: ".7" }}
                          alt="empty cart representation"
                          className="rounded-4 mb-3"
                        />
                      </div>
                    </div>
                    <NavLink to="/allproducts" className="mb-2">
                      <button className="button-34 h2 text-uppercase mt-3 ">
                        Add products <CgArrowLongRight />
                      </button>
                    </NavLink>
                  </>
                )}
              </div>
              {cart?.map((product) => (
                <>
                  <div
                    className="row flex-row   mb-3 rounded-4"
                    style={{
                      boxShadow:
                        "rgba(60, 64, 67, 0.3) 0px 1px 2px 0px, rgba(60, 64, 67, 0.15) 0px 1px 3px 1px",
                    }}
                  >
                    <div className="col-md-3 p-3">
                      <img
                        src={`/api/admin/product/product-photo/${product._id}`}
                        className="card-img-top cardimg    rounded-2"
                        alt={product.productName}
                      />
                    </div>
                    <div className="col-md-8 p-3">
                      <h3 className="text-capitalize fw-bold font-monospace">
                        {product.productName}
                      </h3>
                      <hr />
                      {/* <p>{product.description}</p> */}
                      <div className="d-flex flex-column flex-wrap ">
                        <h4 className="fw-bold">
                          {" "}
                          <FaRupeeSign className="h5 mt-1" />
                          {product.price}/-
                        </h4>
                        <button
                          className="btn btn-danger fw-bold "
                          style={{
                            boxShadow:
                              "rgba(0, 0, 0, 0.4) 0px 2px 4px, rgba(0, 0, 0, 0.3) 0px 7px 13px -3px, rgba(0, 0, 0, 0.2) 0px -3px 0px inset",
                          }}
                          onClick={() => removeCartItem(product._id)}
                        >
                          remove
                        </button>
                      </div>
                    </div>
                  </div>
                </>
              ))}
            </div>
          </div>
          <div className="col-md-4  right-0 text-center">
            <h2 className="fw-bold  text-uppercase font-monospace">
              cart summary
            </h2>{" "}
            <p className="text-capitalize"> total | checkout | payment</p>
            <hr />
            <h4 className="text-uppercase ">
              {" "}
              grand-Total <br />
              <span className="fw-bold  h3"> {totalPrice()} /-</span>
            </h4>
            <hr />
            <div>
              {auth?.user?.role === 1 ? (
                <>
                  <div>
                    {" "}
                    <p className="bg-warning  py-3 fs-5 fw-bold rounded-5">
                      sorry! you need to be a normal user{" "}
                    </p>
                    <button
                      className=" btn btn-outline-danger px-5 py-2 fs-3 "
                      onClick={() => Navigate("/login", { state: "/cart" })}
                    >
                      {" "}
                      Login
                    </button>
                  </div>
                </>
              ) : (
                <>
                  {auth?.user?.address ? (
                    <>
                      <div>
                        <h5>
                          Delevery Address : <br />{" "}
                          <span className="h4 fw-bold px-5">
                            {auth?.user?.address}
                          </span>
                        </h5>
                        <button
                          className="btn btn-outline-warning fs-5"
                          onClick={() => {
                            Navigate("/profile/user/profile");
                          }}
                        >
                          {" "}
                          Change Deleviry Address
                        </button>
                      </div>
                    </>
                  ) : (
                    <>
                      <div>
                        {auth?.token ? (
                          <>
                            <button
                              className="btn btn-outline-warning py-3"
                              onClick={() => Navigate("/profile/user/profile")}
                            >
                              Add Address
                            </button>
                          </>
                        ) : (
                          <>
                            <h3 className="bg-warning py-2">
                              cant make an order! <span>Login First</span>
                            </h3>
                            <button
                              className="btn btn-outline-warning px-5 py-3"
                              onClick={() =>
                                Navigate("/login", { state: "/cart" })
                              }
                            >
                              Login
                            </button>
                          </>
                        )}
                      </div>
                    </>
                  )}
                </>
              )}
            </div>
            <hr />
            {auth?.user?.role === 1 ? (
              " "
            ) : (
              <>
                {auth?.token ? (
                  <div className="container">
                    {!clientToken || !cart.length ? (
                      " "
                    ) : (
                      <>
                        <DropIn
                          options={{
                            authorization: clientToken,
                            paypal: {
                              flow: "vault",
                            },
                          }}
                          onInstance={(instance) => SetInstance(instance)}
                        />
                        <button
                          className="btn btn-primary py-2 px-5"
                          onClick={HandlePayment}
                          disabled={
                            !instance || Loading || !auth?.user?.address
                          }
                        >
                          {" "}
                          {Loading ? "processing...." : "make payment"}
                        </button>
                      </>
                    )}
                  </div>
                ) : (
                  " "
                )}
              </>
            )}
          </div>
        </div>
      </div>
    </div>
  );
};

export default Cart;
